import React from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import Wrapper from './conponent/Wrapper';

const Container = styled.div`
  position: relative;
  width: 1920px;
  height: 1375px;
  background-color: white;
  border-radius: 15px;
  background-color: white;
  justify-content: center;
`;

const ImageContainer = styled.div`
  position: absolute;
  width: 636px;
  height: 788px;
  top: 30px;
  left: 610px;
  background: url('../img/Main.png') center/cover no-repeat;
  box-shadow: 0px 10px 10px 10px rgba(0, 0, 0, 0.5);
  position: relative;
  justify-content: center;
  
  border-radius: 15px;
`;

const ButtonContainer = styled.div`
  position: relative;
  width: 578px;
  height: 66px;
  top: 696px;
  left: 29px;
`;

const Button = styled.button`
  position: relative;
  width: 576px;
  height: 66px;
  background-color: #4637F1;
  border-radius: 15px;
  border: none;
  cursor: pointer; //   
`;

const ButtonText = styled.div`
  position: absolute;
  top: 21px;
  left: 228px;
  font-family: 'Inter-SemiBold', Helvetica;
  font-weight: 600;
  font-size: 20px;
  color: white;
  white-space: nowrap;
`;



//    ...

const MainCalcul = () => {
  const navigate = useNavigate();
  const goToPickPage = () => {
    navigate('/Pickpage'); 
  };

  return (
    <Wrapper>
    <Container>
      <ImageContainer>
        <ButtonContainer>
        <Button onClick={goToPickPage}>
            <ButtonText>  </ButtonText>
          </Button>
        </ButtonContainer>
        {/*  ... */}
      </ImageContainer>
      {/*  ... */}
    </Container>
    </Wrapper>
  );
};

export default MainCalcul;
