import { useNavigate } from 'react-router-dom';
import React, { useState } from 'react';
import styled from 'styled-components';
import Wrapper from './conponent/Wrapper';
import TotalCostCalculator from './TotalCostCalculator';


const ButtonContainer = styled.div`
  position: absolute;
  width: 578px;
  height: 66px;
  top: 956px;
  left: 690px;
`;

const Button = styled.button`
  position: absolute;
  width: 576px;
  height: 66px;
  background-color: #4637F1;
  border-radius: 15px;
  border: none;
  cursor: pointer; //   
`;

const ButtonText = styled.div`
  position: absolute;
  top: 21px;
  left: 228px;
  font-family: 'Inter-SemiBold', Helvetica;
  font-weight: 600;
  font-size: 20px;
  color: white;
  white-space: nowrap;
`;
const CheckboxContainer = styled.div`
  position: absolute;
  top: 850px; //    
  left: 690px;
  width: 545px;
  padding: 15px;
  background-color: #f0f0f0; // 
  font-size : 1rem;
  border-radius: 15px;
`;


const Interior = () => {
  const navigate = useNavigate();
  const [option1Checked, setOption1Checked] = useState(false);
  const [option2Checked, setOption2Checked] = useState(false);


  
 
  const handleOption1Change = (event) => {
    setOption1Checked(event.target.checked);
  };

  const handleOption2Change = (event) => {
    setOption2Checked(event.target.checked);
  };
  
  const isEligibleForConsultation = option1Checked && option2Checked;

  const handleConsultationClick = () => {
    if (!isEligibleForConsultation) {
      alert('  .'); //    alert
    } else {
      navigate('/Kakao'); //      
    }
  };


    return (
      <Wrapper>
      <div>
        {/*   */}
        <CheckboxContainer>
        <div>
          <input
            type="checkbox"
            checked={option1Checked}
            onChange={handleOption1Change}
          /> 
                       .  !
        </div>
        <div>
          <input
            type="checkbox"
            checked={option2Checked}
            onChange={handleOption2Change}
          /> 
              /    /    .  !
        </div>
        </CheckboxContainer>
        
      </div>
      <TotalCostCalculator/>
      <ButtonContainer>
        <Button onClick={handleConsultationClick}> {/*     */}
          <ButtonText>1:1  </ButtonText>
        </Button>
      </ButtonContainer>


      </Wrapper>
      
    );
  };
  
  export default Interior;
  