import React from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import Wrapper from './conponent/Wrapper';

const Container = styled.div`
  width: 1920px;
  height: 1375px;
  position: relative;
  background-color: white;
`;

const FooterContainer = styled.div`
  width: 1920px;
  height: 189px;
  position: absolute;
  left: 0;
  top: 1186px;
  background-color: white;
  border-top: 1px solid #a2a2a2; /* stone-500 */
`;

const FooterText = styled.div`
  width: 484px;
  position: absolute;
  left: 688px;
  top: 50.37px;
  color: #4b5563; /* neutral-600 */
  font-size: 1rem; /* text-base */
  font-family: 'Inter', sans-serif;
`;

const Title = styled.div`
  width: 512px;
  height: 88px;
  position: absolute;
  left: 685px;
  top: 101px;
  text-align: center;
  color: black;
  font-size: 4rem; /* text-5xl */
  font-weight: 800; /* font-semibold */
  font-family: 'Inter', sans-serif;
`;




const ImageContainer2 = styled.div`
position: absolute;
  width: 270.96px;
  height: 250px;
  left: 791px;
  top: 508px;
  box-shadow: 0px 10px 10px 10px rgba(0, 0, 0, 0.3);
  border-radius: 35px;
  cursor: pointer;
  `;

const StyledImage2 = styled.img`
  width: 100%;
  height: 100%;
  border-radius: 35px;
`;

