import React, { useState } from 'react';
import styled from 'styled-components';

const Layout = styled.div`
  display: flex;
  align-items: flex-start;
  padding: 20px;
  flex-direction: row-reverse;
  position:absolute;
  right:800px;
  top:450px;
`;

const ImageSection = styled.div`
  flex:-20;
  padding: 20px;
`;



const FormSection = styled.div`
  flex:2 ;
  padding: 20px;
  background: #f4f7f6;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
`;

const Title = styled.h2`
  margin-bottom: 20px;
  color: #4a5d56;
`;

const SelectGroup = styled.div`
  margin-bottom: 20px;
`;
const Select = styled.select`
  padding: 12px 40px ;
  margin-right: 40px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
`;

const Button = styled.button`
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #a0c4b8; //  
  color: white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  &:hover {
    background-color: #95b1a6;
  }
`;

const CostDisplay = styled.div`
  margin-top: 20px;
  font-size: 18px;
  font-weight: bold;
  color: #667776; //  
  margin-top: 20px;
`;

const Wallpaper =({ onCostChange }) => {

const [selectedSize, setSelectedSize] = useState(15);
  const [selectedType, setSelectedType] = useState('hapji');
  const [calculatedCost, setCalculatedCost] = useState(0);
  const handleSizeChange = (event) => {
    setSelectedSize(event.target.value);
  };

  const handleTypeChange = (event) => {
    setSelectedType(event.target.value);
  };

  const calculateCost = () => {
    const slopeHapji = 2.4594095940959404;
    const interceptHapji = 34.82103321033208;
    const slopeSilk = 6.284132841328411;
    const interceptSilk = 4.752767527675291;
  
    const size = selectedSize;
    let cost = 0;
    if (selectedType === 'hapji') {
      cost = slopeHapji * size + interceptHapji;
    } else if (selectedType === 'silk') {
      cost = slopeSilk * size + interceptSilk;
    }
    const calculatedCostValue = cost.toFixed(2);
    setCalculatedCost(calculatedCostValue);
    onCostChange(parseFloat(calculatedCostValue)); //    parseFloat 
  };
  

  return(
    <Layout>
    <ImageSection>
    </ImageSection>
    <FormSection>
      <Title>  </Title>
      <SelectGroup>
        <Select onChange={handleSizeChange} value={selectedSize}>
          {/* 15 54  */}
          {Array.from({length: 40}, (_, i) => i + 15).map((size) => (
            <option key={size} value={size}>{size}</option>
          ))}
        </Select>
        <Select onChange={handleTypeChange} value={selectedType}>
          <option value="hapji"></option>
          <option value="silk"></option>
        </Select>
        </SelectGroup>
        <Button onClick={calculateCost}> </Button>
        <CostDisplay> : {calculatedCost}</CostDisplay>
      </FormSection>
    </Layout>
   


  )
};
export default Wallpaper;