import React, { useState } from 'react';
import styled from 'styled-components';


const Layout = styled.div`
  display: flex;
  align-items: flex-start;
  padding: 20px;
  flex-direction: row-reverse;
  position:absolute;
  left:200px;
  top:600px;
`;

const ImageSection = styled.div`
  flex:2;
  padding: 20px;
`;



const FormSection = styled.div`
  flex: -1;
  padding: 20px;
  background: #f4f7f6;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
`;

const Title = styled.h2`
  margin-bottom: 20px;
  color: #4a5d56;
`;

const SelectGroup = styled.div`
  margin-bottom: 20px;
`;
const Select = styled.select`
  padding: 12px 40px ;
  margin-right: 25px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
`;

const Button = styled.button`
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #a0c4b8; //  
  color: white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  &:hover {
    background-color: #95b1a6;
  }
`;

const CostDisplay = styled.div`
  margin-top: 20px;
  font-size: 18px;
  font-weight: bold;
  color: #667776; //  
  margin-top: 20px;
`;

const MoldingCal = ({ onCostChange }) => {
  const [selectedNumber, setSelectedNumber] = useState(2); //    
  const [selectedType, setSelectedType] = useState('top'); //    
  const [calculatedCost, setCalculatedCost] = useState(0);

  const calculateCost = () => {
    const slopeTop = 11.6;
    const interceptTop = 19.4;
    const slopeBottom = 8.5;
    const interceptBottom = 14.5;
    const slopeBoth = 19.9;
    const interceptBoth = 30.6;

    let cost = 0;
    if (selectedType === 'top') {
      cost = slopeTop * selectedNumber + interceptTop;
    } else if (selectedType === 'bottom') {
      cost = slopeBottom * selectedNumber + interceptBottom;
    } else if (selectedType === 'both') {
      cost = slopeBoth * selectedNumber + interceptBoth;
    }
    const calculatedCost = cost.toFixed(2); //     
    setCalculatedCost(calculatedCost);
    onCostChange(calculatedCost);
  };

  return (
    <Layout>
    <ImageSection>
    </ImageSection>
    <FormSection>
      <Title>   </Title>
      <SelectGroup>
        <Select onChange={e => setSelectedNumber(e.target.value)} value={selectedNumber}>
          {[2, 3, 4, 5, 6].map(number => (
            <option key={number} value={number}>{number}()</option>
          ))}
        </Select>
        <Select onChange={e => setSelectedType(e.target.value)} value={selectedType}>
          <option value="top"> </option>
          <option value="bottom"> </option>
          <option value="both">+ </option>
        </Select>
        </SelectGroup>
        <Button onClick={calculateCost}> </Button>
        <CostDisplay> : {calculatedCost}</CostDisplay>
      </FormSection>
    </Layout>

  );
};

export default MoldingCal;
