import React, { useState } from 'react';
import styled from 'styled-components';


const Layout = styled.div`
  display: flex;
  align-items: flex-start;
  padding: 20px;
  flex-direction: row-reverse;
  position:absolute;
  right:200px;
  top:300px;
`;

const ImageSection = styled.div`
  flex:-2;
  padding: 20px;
`;



const FormSection = styled.div`
  flex: 1;
  padding: 20px;
  background: #f4f7f6;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  
`;

const Title = styled.h2`
  margin-bottom: 20px;
  color: #4a5d56;
`;

const SelectGroup = styled.div`
  margin-bottom: 20px;
`;
const Select = styled.select`
  padding: 12px 40px ;
  margin-right: 40px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
`;

const Button = styled.button`
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #a0c4b8; //  
  color: white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  &:hover {
    background-color: #95b1a6;
  }
`;

const CostDisplay = styled.div`
  margin-top: 20px;
  font-size: 18px;
  font-weight: bold;
  color: #667776; //  
  margin-top: 20px;
`;

const HdfCal = ({ onCostChange }) => {
    const [selectedSize, setSelectedSize] = useState(15); //   
    const [selectedOption, setSelectedOption] = useState('living'); //    
    const [calculatedCost, setCalculatedCost] = useState(0);
  
    //     
    const calculateCost = () => {
      //      y
      const slopeLiving = 3.6330275229357807;
      const interceptLiving = 31.66055045871559;
      const slopeFull = 7.681796233703529;
      const interceptFull = 0.14469660389500358;
  
      let cost = 0;
      if (selectedOption === 'living') {
        cost = slopeLiving * selectedSize + interceptLiving;
      } else if (selectedOption === 'full') {
        cost = slopeFull * selectedSize + interceptFull;
      }
      const calculatedCost = cost.toFixed(2); //     
    setCalculatedCost(calculatedCost);
    onCostChange(calculatedCost);
    };
  
    return (
      <Layout>
      <ImageSection>
      </ImageSection>
      <FormSection>
        <Title>   </Title>
        <SelectGroup>
          <Select onChange={e => setSelectedSize(e.target.value)} value={selectedSize}>
            {Array.from({length: 40}, (_, i) => i + 15).map((size) => (
              <option key={size} value={size}>{size}</option>
            ))}
          </Select>
          <Select onChange={e => setSelectedOption(e.target.value)} value={selectedOption}>
            <option value="living"></option>
            <option value="full">+ </option>
          </Select>
          </SelectGroup>
        <Button onClick={calculateCost}> </Button>
        <CostDisplay> : {calculatedCost}</CostDisplay>
      </FormSection>
    </Layout>
    );
  };
  
  export default HdfCal;