import React from 'react';
import styled from 'styled-components';
import Wrapper from './conponent/Wrapper';

const Container = styled.div`
  width: 1920px;
  height: 1375px;
  position: relative;
  background-color: white;
`;

const FooterContainer = styled.div`
  width: 1920px;
  height: 189px;
  position: absolute;
  left: 0;
  top: 1186px;
  background-color: white;
  border-top: 1px solid #a2a2a2; /* border-stone-500 */
`;

const FooterText = styled.div`
  width: 484px;
  height: 3px; /* h-3 */
  position: absolute;
  left: 688px;
  top: 62.37px;
  color: #4b5563; /* text-neutral-600 */
  font-size: 1rem; /* text-base */
  font-family: 'Inter', sans-serif;
`;

const InfoText = styled.div`
  width: 698px;
  height: 173px;
  position: absolute;
  left: 601px;
  top: 56px;
  text-align: center;
  color: black;
  font-family: 'Inter', sans-serif;
  span {
    display: block; /* For line breaks */
    font-weight: 600; /* font-semibold */
    &:first-child {
      font-size: 2.5rem; /* text-5xl */
    }
    &:last-child {
      font-size: 2.25rem; /* text-4xl */
    }
  }
`;

const QRCodeText = styled.div`
  width: 698px;
  height: 173px;
  position: absolute;
  left: 590px;
  top: 802px;
  text-align: center;
  color: black;
  font-size: 2.5rem; /* text-5xl */
  font-weight: 600; /* font-semibold */
  font-family: 'Inter', sans-serif;
`;

const StyledImage = styled.img`
  width: 648px;
  height: 594px;
  position: absolute;
  left: 626px;
  top: 210px;
`;


const Kakao = () => {
  return (
    <Wrapper>
    <Container>
      <FooterContainer>
        <FooterText> |   |  | </FooterText>
      </FooterContainer>
      <InfoText>
        <span>    !<br /></span>
        <span> -  : 09:00 ~ 18:00<br /></span>
      </InfoText>
      <QRCodeText>QR     !</QRCodeText>
      <StyledImage src="/img/KakaoQR.jpg" alt="QR" />
    </Container>
    </Wrapper>
  );
};

export default Kakao;
