import React, { useState } from 'react';
import styled from 'styled-components';


const Layout = styled.div`
   display: flex;
  align-items: flex-start;
  padding: 20px;
  flex-direction: row-reverse;
  position:absolute;
  left:200px;
  top:300px;
`;

const ImageSection = styled.div`
  flex: 2;
  padding: 20px;
`;



const FormSection = styled.div`
  flex: -1;
  padding: 20px;
  background: #f4f7f6;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  
`;

const Title = styled.h2`
  margin-bottom: 20px;
  color: #4a5d56;
`;

const SelectGroup = styled.div`
  margin-bottom: 20px;
`;
const Select = styled.select`
  padding: 12px 40px ;
  margin-right: 40px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
`;

const Button = styled.button`
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #a0c4b8; //  
  color: white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  &:hover {
    background-color: #95b1a6;
  }
`;

const CostDisplay = styled.div`
  margin-top: 20px;
  font-size: 18px;
  font-weight: bold;
  color: #667776; //  
  margin-top: 20px;
`;

const FloorCal = ({ onCostChange }) => {
  const [selectedSize, setSelectedSize] = useState(15); //   
  const [selectedType, setSelectedType] = useState('1.8mm'); //    
  const [calculatedCost, setCalculatedCost] = useState(0);

  //    
  const calculateCost = () => {
    // :    
    const slope18mm = 1.6275454545454546;
    const intercept18mm = 9.036363636363635;
    const slope22mm = 2.268181818181818;
    const intercept22mm = 17.227272727272727;
    const slope45mm = 4.690909090909091;
    const intercept45mm = 44.77272727272727;

    let cost = 0;
    if (selectedType === '1.8mm') {
      cost = slope18mm * selectedSize + intercept18mm;
    } else if (selectedType === '2.2mm') {
      cost = slope22mm * selectedSize + intercept22mm;
    } else if (selectedType === '4.5mm') {
      cost = slope45mm * selectedSize + intercept45mm;
    }

    
    const calculatedCost = cost.toFixed(2); //     
    setCalculatedCost(calculatedCost);
    onCostChange(calculatedCost);
  };

  return (
    <Layout>
    <ImageSection>
    </ImageSection>
    <FormSection>
      <Title>  </Title>
      <SelectGroup>
      <Select onChange={e => setSelectedSize(e.target.value)} value={selectedSize}>
        {Array.from({length: 40}, (_, i) => i + 15).map((size) => (
          <option key={size} value={size}>{size}</option>
        ))}
      </Select>
      <Select onChange={e => setSelectedType(e.target.value)} value={selectedType}>
        <option value="1.8mm">1.8mm()</option>
        <option value="2.2mm">2.2mm()</option>
        <option value="4.5mm">4.5mm()</option>
      </Select>
      </SelectGroup>
        <Button onClick={calculateCost}> </Button>
        <CostDisplay> : {calculatedCost}</CostDisplay>
      </FormSection>
    </Layout>
  );
};

export default FloorCal;
